import common from '@/libs/common.mixin'
import QrPopup from './qr-popup.vue'	

export default{
	mixins: [ common ],
	components: {
		QrPopup
	},
	data () {
		return {
			data: []
		}
	},
	computed: {
		style () {
			return {
				top: `${this.headerHeight}px`
			}
		},
		data_uri () {
			return `market_channels?create_uid=${this.user$.uid}`
		}
	},
	methods: {
		showQR (item) {
			this.$refs.qr
			.set('mc_id',item.mc_id)
			.show('')
		},
		channelDetail (item) {
			this.$router.push({path:`/recruit/channel/${item.mc_id}`})
		}
	}
}	




