
import modal from '@/libs/modal.mixin'
import common from '@/libs/common.mixin'
import Popup from 'vux/src/components/popup/index.vue'
import Qrcode from 'vux/src/components/qrcode/index.vue'

export default{
mixins: [ common, modal ],
components: {
Popup,
Qrcode
},
data () {
return {
mc_id: 0
}
},
computed: {
style () {
return {
height: '100%',
width: '100%',
background: '#fff'
}
},
src () {
let base_url = this.$store.state.base_url,
sub_host = this.$store.state.user.info?this.$store.state.user.info.x_sub_host:''

if(base_url.indexOf(sub_host)==-1){
base_url = base_url.replace('//','//'+sub_host+'.')
}

return `${base_url}/m#/qrsign?mc_id=${this.mc_id}&bid=${this.bid$[0]}&eid=${this.eid$}`
}
}
}



